<template>
    <div class="change-bg">
        <div class="change-bg-header">
            <i class="iconfont albumzuojiantou" @click="$router.push('/mine')"></i>
            <span></span>
        </div>
        <div class="bg-header"></div>
        <div class="change-bg-content clearfix">
            <span style="font-size:12px;color:#666;"></span>
            <ul>
                <li v-for="(item,index) in bgData" :key="index">
                    <div class="bg-card">
                        <img :src="item.bgSrc">
                        <div class="using-btn" v-show="bgIndex==index">
                            
                        </div>
                        <div class="setbg-btn" @click="setBg(index)" v-show="!(bgIndex==index)">
                            
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ChangeBg",
        data() {
            return {
                bgData:[
                    {index:0,bgSrc:require("../../assets/bg/bg0.jpg")},
                    {index:1,bgSrc:require("../../assets/bg/bg1.jpg")},
                    {index:2,bgSrc:require("../../assets/bg/bg2.jpg")},
                    {index:3,bgSrc:require("../../assets/bg/bg3.jpg")},
                    {index:4,bgSrc:require("../../assets/bg/bg4.jpg")},
                    {index:5,bgSrc:require("../../assets/bg/bg5.jpg")},
                    {index:6,bgSrc:require("../../assets/bg/bg6.jpg")},
                    {index:7,bgSrc:require("../../assets/bg/bg7.jpg")},
                    {index:8,bgSrc:require("../../assets/bg/bg8.jpg")},
                    {index:9,bgSrc:require("../../assets/bg/bg9.jpg")}
                ],
                bgIndex:localStorage.getItem('bgIndex')
            }
        },
        created() {},
        methods: {
            setBg(index) {
                localStorage.setItem('bgIndex',index);
                this.bgIndex = index;
                this.$toast({
                    message:'',
                    position:'bottom'
                });
            }
        }
    }
</script>

<style scoped lang="scss">
.change-bg {
    .change-bg-header {
        height: 80px;
        width: 100%;
        border-bottom: 0.5px solid #eee;
        position: fixed;
        top: 0;
        color: #fff;
        z-index: 999;
        background-color: #1a497d;
        i {
            font-size: 26px;
            position: absolute;
            bottom: 15px;
            left: 15px;
        }
        span {
            position: absolute;
            left: 60px;
            bottom: 20px;
        }
    }
    .bg-header {
        height: 50px;
    }
    .change-bg-content {
        background-color: #eee;
        padding: 15px;
        ul {
            list-style: none;
            margin-top: 10px;
            li {
                width: 48%;
                padding-bottom: 48%;
                height: 0;
                float: left;
                margin-bottom: 15px;
                border-radius: 5px;
                overflow: hidden;
                .bg-card {
                    position: relative;
                    .using-btn,
                    .setbg-btn {
                        font-size: 10px;
                        color: #ffffff;
                        position: absolute;
                        bottom: 10px;
                        right: 10px;
                        padding: 0px 5px 1px 5px;
                        border: 1px solid #ffffff;
                        border-radius: 2px;
                    }
                    .setbg-btn {
                        background-color: #008B00;
                        border: 1px solid #008B00;
                    }
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            li:nth-child(2n) {
                float: right;
            }
        }
    }
    .clearfix:before,
    .clearfix:after {
        content: "";
        /* BFC,  */
        display: table;
    }
    .clearfix:after {
        clear: both;
    }
    .clearfix {
        /* *    ie 6 7   *IE67   zoomie67 */
        *zoom: 1;
    }
}
</style>



