export function addAlbum(name, visiblePermissionId) {
    return request({
        url: '/album/album/add',
        method: 'post',
        data: JSON.stringify({
            name: name,
            visiblePermissionId: visiblePermissionId
        }),
        headers: {
            'Content-Type': 'application/json;'
        }
    })
}

