export function deletePhoto(data) {
    return request({
        url: '/album/image/delete?' + test(data),
        method: 'post',
    })
}


function test(data) {
    let url = "";
    for (let i in data.imageIds) {
        if (i == 0) {
            url += "imageIds%5B%5D=" + data.imageIds[i]
        } else {
            url += "&imageIds%5B%5D=" + data.imageIds[i]
        }
    }
    url += "&albumId=" + data.albumId;
    return url;
}

